.home__desc{
    flex-basis: 50%;
}

.home__image{
    flex-basis: 33%;
} */


.home__social{
    display: flex;
    flex-flow: column;
    justify-content: flex-start;
    row-gap: 0.5rem;
}

.home__social-icon{
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover{
    color: var(--title-color-dark);
}

.home__title{
    font-size: var(--big-font-size);
    margin-bottom: var(--mb-0-25);
}

.home__hand{
    width: 38px;
    height: 38px;
    margin-left: 0.5rem;
}

.home__subtitle{
    position: relative;
    padding-left: 5.4rem;
    margin-bottom: var(--mb-1);
    font-size: var(--h3-font-size);
    font-weight: var(--font-normal);
}

.home__subtitle::before{
    content: '';
    position: absolute;
    width: 70px;
    height: 2px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;
}

.home__description{
    max-width: 450px;
    margin-bottom: var(--mb-3);
}


.home__image-profile{
    border-radius: 50%;
    border: 3px solid var(--text-color);
    width: 300px;
    height: 300px;
}

.home__scroll{
    text-align: center;
}

.home__scroll--name{
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: var(--mb-0-25);

}

.home__scroll--arrow{
    color: var(--title-color);
    font-size: 1.25rem;
    font-weight: var(--font-medium);
}


.home__sroll--mouse {
    color: var(--title-color);
    font-size: 2rem;
    font-weight: var(--font-medium);
}
/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .home__container {
            row-gap: 8rem;
        }

    .home__hand{
        width: 25px;
        height: 25px;
    }
    .home__subtitle{
        padding-left: 3.75rem;
        margin-bottom: var(--mb-1);
    }

    .home__subtitle::before{
        width: 42px;
        top:0.8rem
    }

    .home__image-profile {
        width: 250px;
        height: 250px;
    }
}

@media screen and (max-width: 768px) {
    .home__container {
            row-gap: 3rem;
        }
    .home__content {
        justify-content: space-around;
        justify-items: center;
        padding-top: 2rem;

    }


    .home__img{
        order:initial;
        justify-self: initial;
    }
    .home__image-profile {
        width: 250px;
        height: 250px;
    }
    
    .home__image-profile {
        width: 200px;
        height: 200px;
        }
        .home__desc {
            order:1;
            flex-basis: 100%;
            
        }
    
        .home__image {
            order: 0;
        }

        .home__description {
            max-width: 100%;
            margin-bottom: var(--mb-2);
        }
    
        
}


@media screen and (max-width: 350px) {
    .home__container {
            row-gap: 2rem;
        }
    .home__image-profile {
            width: 180px;
            height: 180px;
        }
    .home__hand{
        width: 22px;
        height: 22px;
    }

    .home__description {
        max-width: 100%;
        margin-bottom: var(--mb-1);
    }

}